import classNames from 'classnames';

type BottomNavigationProps = {
  vertical?: boolean
};

export const BottomNavigation = ({ vertical }: BottomNavigationProps) => {
  const navigationClasses = classNames('bottom-navigation', {
    'bottom-navigation__vertical': vertical,
  });

  return (
    <div className={navigationClasses}>
      <a
        className="bottom-navigation__link"
        // eslint-disable-next-line max-len
        href="https://github.com/AmadeuszAndroid/react_phone-catalog/tree/develop"
      >
        Github
      </a>

      <a className="bottom-navigation__link" href="#/">Contacts</a>

      <a className="bottom-navigation__link" href="#/">rights</a>
    </div>
  );
};

BottomNavigation.defaultProps = {
  vertical: false,
};
